import Layout from '../layout'
import Karaoke from '../components/karaoke'
export default [
  {
    path: '/',
    redirect: '/dashboard',
    name: 'Home',
    component: Layout,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: Karaoke
      }
    ]
  }
]
