import draggable from 'vuedraggable'
import Search from '../search'
import swal from 'sweetalert'
import LocalStorage from '../../helper/LocalStorage'
export default {
  name: 'karaoke',
  components: {
    draggable,
    Search
  },
  props: [],
  data() {
    return {
      videoId: '',
      playerVars: {
        autoplay: 1
      },
      play_list: []
    }
  },
  computed: {

  },
  mounted() {
    this.play_list = LocalStorage.getAll()
  },
  methods: {
    playing() {
      // eslint-disable-next-line no-console
      console.log(' we are watching!!!')
    },
    ended() {
      this.videoId = 'NOPotPS60o8'
    },
    update() {
      LocalStorage.updateItem(this.play_list)
    },
    play(item) {
      this.videoId = item.id
    },
    addVideo(item) {
      LocalStorage.addItem(item)
      if (this.play_list.indexOf(item) !== -1) {
        this.$notify({
          group: 'success',
          type: 'error',
          title: 'Thông báo',
          text: 'Đã có trong list nhạc'
        })
      } else {
        this.play_list.push(item)
        this.$notify({
          group: 'success',
          title: 'Thông báo',
          text: 'Thêm thành công'
        })
      }

    },
    destroy(item) {
      LocalStorage.destroyItem(item)
      this.play_list = this.play_list.filter(video => {
        return item.id !== video.id
      })
      this.$notify({
        group: 'success',
        title: 'Thông báo',
        text: 'Xóa thành công'
      })
    },
    clearAll () {
      swal({
        title: 'Xác nhận xóa ?',
        icon: 'warning',
        buttons: true,
        dangerMode: true
      })
        .then((willDelete) => {
          if (willDelete) {
             this.play_list = []
              LocalStorage.clearAll()
          }
        })
    },
    top (item) {
      this.play_list = this.play_list.filter(video => {
        return item.id !== video.id
      })
      this.play_list.unshift(item)
      LocalStorage.top(item)
    }
  }
}
