import lodash from 'lodash'
import {HTTP} from '../../helper/CallAPI'
import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
import $ from "jquery"
export default {
  name: 'search',
  components: {
    VueBootstrapTypeahead
  },
  props: [],
  data () {
    return {
      txtSearch: 'Karaoke',
      result: [],
      suggestions: ['Karaoke']
    }
  },
  computed: {

  },
  mounted () {
    HTTP.get('search', {
      params: {
        part: 'snippet,id',
        q: 'karaoke',
        maxResults: 50,
        type: 'video',
        key: process.env.VUE_APP_API_KEY
    }
    }).then(res => {
      this.result = res.data.items.map(item => {
        var data = {}
        data.id = item.id.videoId;
        data.title = item.snippet.title;
        data.thumb = item.snippet.thumbnails.high.url;
        data.channelTitle = item.snippet.channelTitle;
        data.description = item.snippet.description
        return data
      })
    }).catch(errors => {
      // eslint-disable-next-line no-console
      console.log(errors)
    })
  },
  watch: {
    txtSearch () {
      this.s()
    }
  },
  created: function () {
    this.s = lodash.debounce(this.getSuggestions, 500)
  },
  methods: {
    getSuggestions: function () {
      var vm = this
      $.ajax({
        url: "https://suggestqueries.google.com/complete/search?hl=en&ds=yt&client=youtube&hjson=t&cp=1&q="+ this.txtSearch +"&key="+ process.env.VUE_APP_API_KEY +"&format=5&alt=json&callback=?",  
        dataType: 'jsonp',
        success: function(data) {
          vm.suggestions = data[1].map(item => {
             return item[0]
           })
        }
    });
    },
    search: function () {
      this.suggestions = []
      HTTP.get('search', {
        params: {
          part: 'snippet,id',
          q: this.txtSearch,
          maxResults: 50,
          type: 'video',
          key: process.env.VUE_APP_API_KEY
      }
      }).then(res => {
        this.result = res.data.items.map(item => {
          var data = {}
          data.id = item.id.videoId;
          data.title = item.snippet.title;
          data.thumb = item.snippet.thumbnails.high.url;
          data.channelTitle = item.snippet.channelTitle;
          data.description = item.snippet.description
          return data
        })
      }).catch(errors => {
        // eslint-disable-next-line no-console
        console.log(errors)
      })
    },
    selectedSearch () {
      this.suggestions = []
      this.search()
    },
    addVideo (item) {
      this.$emit('addVideo', item)
    }
  }
}
